// import 'antd/dist/antd.min.css';
import { Select } from 'antd';
import './App.css';
import { Button, Space, Popover } from 'antd';
import React, { useRef, useState } from 'react';
import { CloseOutlined } from '@ant-design/icons';
import { pinyin } from 'pinyin-pro';
import { languageTable } from './data';

const { Option } = Select;
function App() {
  const [isLoading, setLoading] = useState(false);
  const [isInputActive, setInputActive] = useState(false);
  const [isShowClearBtn, setClearBtnStatus] = useState(false);
  const [translateText, setTranslateText] = useState('');
  const [translateText1, setTranslateText1] = useState('');
  const [srcValue, setSrcValue] = useState('en');
  const [targetValue, setTargetValue] = useState('zh');
  const leftInputRef= useRef(null);
  const textareaRef = useRef(null);

  
  const onLoadingClick = () => {
    setLoading(true);
    const prompt = textareaRef.current.value.trim();
    if(!prompt) {
      setLoading(false);
      return;
    }

    const data = { 
      src: srcValue,
      tar: targetValue,
      prompt
    };
    
    fetch('/v1/engines/175b/completions', {
      method: 'POST', // or 'PUT'
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify(data),
    })
    .then(response => response.json())
    .then(data => {
      const responseText = data["choices"][0]["text"];
      const responseText1 = data["choices"][1]["text"];
      setTranslateText(responseText);
      setTranslateText1(responseText1);
      setLoading(false);
      
    })
    .catch((error) => {
      setLoading(false);
    });
  };

  const isParent =  (obj, parentObj) => {
    while (obj !== undefined && obj !== null && obj.tagName.toUpperCase() !== 'BODY'){
      if (obj === parentObj){
        return true;
      }
      obj = obj.parentNode;
    }
    return false;
  }

  const onFullAreaClick = (e) => { 
    if(!isParent(e.target, leftInputRef.current)) {
      setInputActive(false);
    }
  }

  const onMouseEnterInputArea = () => {
    setInputActive(true);
  }

  const onMouseLeaveInputArea = () => {
    const currentActiveElement = document.activeElement;
    if(currentActiveElement === textareaRef.current) {
      setInputActive(true);
    }else {
      setInputActive(false);
    }
    
  }

  const onSrcValueChange = (value) => {
    setSrcValue(value);
  }

  const onTargetValueChange = (value) => {
    setTargetValue(value);
  }

  const onInputChange = (e) => {
    const scrollHeight = textareaRef.current.scrollHeight;
    textareaRef.current.style.height = `${scrollHeight}px`;
    const currentValue = e.target.value.trim();
    setClearBtnStatus(currentValue ? true : false);
    // const inputValue = e.target.value;
    // setTranslateText(inputValue);
  }

  const onClearClick = () => {
    setClearBtnStatus(false);
    textareaRef.current.value = '';
    setTranslateText('');
    setTranslateText1('');
    textareaRef.current.focus();
    textareaRef.current.style.height = '162px';
  }

  const filterFun = (input, option) => {
    const chinese2pinyin = pinyin(option.value, { toneType: 'none', type: 'array' }).join('').toLowerCase();
    const abs2pinyin = pinyin(option.abs, { toneType: 'none', type: 'array' }).join('').toLowerCase();
    const en2pinyin = pinyin(option.en, { toneType: 'none', type: 'array' }).join('').toLowerCase();
    const inputPinyin = pinyin(input, { toneType: 'none', type: 'array' }).join('').toLowerCase();
    return  chinese2pinyin.includes(inputPinyin) || abs2pinyin.includes(inputPinyin) || en2pinyin.includes(inputPinyin) || option.pinyin.includes(inputPinyin);
  }

  return (
    <div className="App" onClick={onFullAreaClick}>
      <div className="App-main">
        <div className="App-header-wrap">
          <div className="App-header">
            <div className="App-header-import"></div>
            <div className="App-header-sub">CONE</div>
          </div>
          
        </div>
        <div className="App-main-inner">
          <div className="App-trans-operation-wrapper">
            <div className="App-left-selet">
              <Select
                showSearch
                style={{ width: 338 }}
                defaultValue={srcValue}
                placeholder="Search to Select"
                optionFilterProp="children"
                filterOption={(input, option) => filterFun(input, option)}
                onChange={(value)=> onSrcValueChange(value)}
              >
                {
                  languageTable.map((item,index)=>{
                    return <Option key={`${index}${item.en}`} value={item.abs} en={item.en} abs={item.abs} pinyin={item.pinyin}>{`${item.chinese}(${item.en})`}</Option>
                  })
                }
              </Select>
            </div>
            <div className="App-select-icon-wrap">
              <span className="App-select-icon"></span>
            </div>
            <div className="App-right-selet">
              <Select
                showSearch
                style={{ width: 342 }}
                defaultValue={targetValue}
                placeholder="Search to Select"
                optionFilterProp="children"
                filterOption={(input, option) => filterFun(input, option)}
                onChange={(value)=> onTargetValueChange(value)}
              >
                {
                  languageTable.map((item,index)=>{
                    return <Option key={`${index}${item.en}`} value={item.abs} en={item.en} abs={item.abs} pinyin={item.pinyin}>{`${item.chinese}(${item.en})`}</Option>
                  })
                }
              </Select>
            </div>

            <div className="App-submit">
              <Space style={{ width: '100%' }}>
                <Button type="primary" style={{width: "80px", background: "#F35D9D"}} loading={isLoading} onClick={() => onLoadingClick()}>
                  
                </Button>
              </Space>
            </div>
          </div>

          <div className="App-translateio">
            
            <div className="App-trans-left" ref={leftInputRef} onMouseEnter={onMouseEnterInputArea} onMouseLeave={onMouseLeaveInputArea}>
              <div className={`App-trans-input-wrap ${isInputActive ? 'App-trans-input-wrap-active': ''}`}>
                <div className="App-input-wrap"  dir="ltr">
                  <div className="App-textarea-wrap">
                    <div className="App-textarea-wrap-inner">
                      <textarea className="App-textarea"  ref={textareaRef} placeholder="" onChange={(e)=>{onInputChange(e)}}></textarea>
                    </div>
                  </div>
                  <span className="App-clear-btn" style={{display: isShowClearBtn ? 'block' : 'none'}}>
                    <CloseOutlined onClick={onClearClick} style={{fontSize: 20, color: "#9C9C9C"}}/>
                  </span>
                </div>
              </div>
            </div>


          <Popover content="" placement="bottom">
            <div className="App-trans-middle">
              <div className="App-trans-right-text-wrap">
                <p className="App-trans-right-text">{translateText}</p>
              </div>
            </div>
          </Popover>

            
            
          <Popover content="" placement="bottom">
          <div className="App-trans-right">
              <div className="App-trans-right-text-wrap">
                <p className="App-trans-right-text">{translateText1}</p>
              </div>
            </div>
          </Popover>

            

          </div>
        </div>
      </div>
    </div>
  );
}

export default App;
